.trainer {
  position: relative;
  background-color: $color-nero;
  overflow: hidden;
}

.trainer__container {
  position: relative;
  background-color: $color-default-white;
  padding-bottom: 109.5px;


  @include vp-1199 {
    padding-bottom: 101px;
  }

  @include vp-767 {
    padding-bottom: 68px;
  }
}

.trainer__title {
  color: $color-sapphire;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0 0 51px 0;
  text-align: start;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 34px 0;
    text-align: center;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 45px 0;
  }
}

.trainer__wrapper {
  display: flex;
  flex-wrap: nowrap;
}


